import React from 'react'
import "./CategorySlider.scss"
import Carousel from "react-elastic-carousel"
import PublicItem from '../PublicItem/PublicItem';

export default function CategorySlider({pepsi,category,slides, arrow}) {
    
    return (
        <div className="categorySlider">
            <h2>{category}</h2>
            <Carousel itemsToShow={slides} showArrows= {arrow}>
                <PublicItem image={pepsi} price={200} item="Pepsi"/>
                <PublicItem image={pepsi} price={200} item="Pepsi"/>
                <PublicItem image={pepsi} price={200} item="Pepsi"/>
                <PublicItem image={pepsi} price={200} item="Pepsi"/>
                <PublicItem image={pepsi} price={200} item="Pepsi"/>
                <PublicItem image={pepsi} price={200} item="Pepsi"/>
                <PublicItem image={pepsi} price={200} item="Pepsi"/>
            </Carousel>
        </div>
    )
}
